import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import MentorManagerProfile from "./MentorManagerProfile";
import style from "../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
const MentorExport = lazy(() => import("./MentorExport/MentorExport"));
const MeetingDetail = lazy(() =>
  import(
    "../../../MembersTab/staff/Mainbody/DepartmentHead/DepartmentMentor/DepartmentQueries/MeetingDetail"
  )
);
const QueryDetail = lazy(() =>
  import(
    "../../../MembersTab/staff/Mainbody/DepartmentHead/DepartmentMentor/DepartmentQueries/QueryDetail"
  )
);
const StaffMentorMeeting = lazy(() =>
  import("../../../MembersTab/staff/Mainbody/StaffMentor/StaffMentorMeeting")
);
const DepartmentQueries = lazy(() =>
  import(
    "../../../MembersTab/staff/Mainbody/DepartmentHead/DepartmentMentor/DepartmentQueries/DepartmentQueries"
  )
);
const StaffMenteeList = lazy(() =>
  import("../../../MembersTab/staff/Mainbody/StaffMentor/StaffMenteeList")
);

const MentorManager = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") && !searchParams.get("a") ? (
            <MentorManagerProfile />
          ) : searchParams.get("type") === "mentee" ? (
            <UserMemberContainerWrapper>
              <UserMemberContentWrapper>
                <div className={style.fm_header_container}>
                  <h6>{t("mentee_list")}</h6>
                </div>
                <StaffMenteeList
                  mid={getQuery.state?.onClickValue?._id}
                  departmentId={getQuery.state?.onClickValue?.department?._id}
                />
              </UserMemberContentWrapper>
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "queries" ? (
            <UserMemberContainerWrapper>
              <UserMemberContentWrapper>
                <div className={style.fm_header_container}>
                  <h6>{t("queries")}</h6>
                </div>
                <DepartmentQueries
                  did={getQuery.state?.onClickValue?._id}
                  carryParentState={getQuery.state}
                  queryFlow="By_Mentor"
                />
              </UserMemberContentWrapper>
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "meeting" ? (
            <UserMemberContainerWrapper>
              <UserMemberContentWrapper>
                <div className={style.fm_header_container}>
                  <h6>{t("meeting")}</h6>
                </div>
                <StaffMentorMeeting
                  mid={getQuery.state?.onClickValue?._id}
                  carryParentState={getQuery.state}
                  queryFlow="By_Mentor"
                />
              </UserMemberContentWrapper>
            </UserMemberContainerWrapper>
          ) : searchParams.get("a") === "query-detail" ? (
            <QueryDetail queryFlow="By_Mentor" />
          ) : searchParams.get("a") === "meeting-detail" ? (
            <MeetingDetail />
          ) : searchParams.get("type") === "export" ? (
            <MentorExport mid={getQuery.state?.onClickValue?._id} />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default MentorManager;
